.custom-button{
    min-width: 165px;
    width: auto;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
 
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bolder;
    cursor: pointer;


    &:hover{
        background-color: white;
        color: black;
        border: 1px solid black;
    }

    &.google-sign-in{
        margin-left: 20px;
        border: none;
           margin-bottom: -2px;
        background-color: #4285f4;

        &:hover{
            border: none;
           
            color: white;
        }
    
    }
}

